<template>
  <v-container>
    <v-row v-if="!auth" class="text-center">
      <v-col>
        <v-progress-circular
          :size="100"
          indeterminate
          color="primary"
        ></v-progress-circular>
        <h2>Ta cargando pe</h2>
      </v-col>
    </v-row>
    <v-row v-if="auth">
      <v-col cols="12" class="text-center">
        <h1>{{emoji}} {{ messagesub }} {{emoji}}</h1>
      </v-col>
    </v-row>
    <v-row v-if="auth">
      <v-col cols="6">
        <v-text-field
          label="Ingrese el code, solo asdiegoYA par de mongoles"
          v-model="code"
        ></v-text-field>
      </v-col>
      <v-col cols="6" class="text-center">
        <v-btn @click="createToken()">Ingresar Code</v-btn>
      </v-col>
      <v-col class="text-center" style="color: black" cols="8">
        Habilitar mensaje 
        <v-btn v-if="active != null" :color="active ? 'success' : 'error'" @click="enableMessage">
          <template v-if="active"> Activado </template>
          <template v-else> Desactivado </template>
        </v-btn>
      </v-col>
      <v-col cols="2">
        <v-select
          v-model="emoji"
          :items="itemsEmoji"
          @change="emojichange"
          label="Selecionar emoji"
          dense
          solo
        ></v-select>
      </v-col>
      <v-col cols="12"></v-col>
      <v-col v-if="active != null && !active" cols="8" offset="2" class="text-center">
        <v-text-field
          label="Escribe cualquier webada, solo moderadores..."
          solo
          maxlength="38"
          v-model="messagesub"
          @keyup="changeMessageRandomClient"
        ></v-text-field>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: "User_Home",
  data: () => ({
    messagesub: "",
    auth: false,
    active: null,
    token: null,
    code: "",
    emoji: "🙂",
    itemsEmoji: [
      '🙂',
      '💙',
      '😎',
      '❤️',
      '😏',
      '😍',
      '🥺',
      '🤓',
      '🤩',
      '💩',
      '💀',
      '🤨',
      '🧐',
      '💪',
      '🎈🎈🎈',
      '🎂',
      '🎼',
      '⚠',
      '💲',
      '🚗',
      '🎤',
      '💝',
      '⚽',
      '🙉',
      '🤡',
      '🤣',
      '🙈',
      '🙉',
      '👺',
      '🐽',
      '🤬',
      '☢',
      '👀',
      '😈',
      '👿',
      '👾',
      '👽',
      'F',
      '⚽',
      '🥑',
      '🌈',
      '👀'
    ]
  }),
  sockets: {
    async connect() {},

    async disconnect() {
      console.log("Esta desconectado");
    },
    changeMessageRandom: async function (data) {
      var flag = await this.verifyToken();
      if (flag) {
        this.messagesub = data;
      }
    },
    changeMessage: async function (data) {
      var flag = await this.verifyToken();
      if (flag) {
        this.active = data;
      }
    },
    emojiChangeServer: async function (data) {
      var flag = await this.verifyToken();
      if (flag) {
        this.emoji = data;
      }
    }
  },
  async mounted() {
    this.token = window.localStorage.getItem("token-asdiegoYA");
    if (this.token == null) {
      window.location.href = "/";
    }
    await this.verifyToken();
  },
  methods: {
    emojichange(){
      this.$socket.emit("emojiChangeClient", this.emoji);
    },
    async verifyToken() {
      var api = this.$store.state.URL + "/auth/home";
      var response = await this.axios.get(api, {
        headers: {
          "x-access-token": this.token,
        },
      });

      if(response) {
        var data = response.data;
        this.auth = data.auth;
        if (!data.auth) {
          window.localStorage.removeItem('token-asdiegoYA');
          window.location.href = "/";
        }
      }
      return this.auth
    },
    async createToken() {
      var flag = await this.verifyToken();
      if (flag) {
        var api =
          this.$store.state.URL +
          "/channels/myTokenStreamlabs?code=" +
          this.code;

        var response = await  this.axios.get(api);
        if(response) {
          if(response.data){
            this.code = response.data.message;
          }
        } else {
          this.code = "F de los F"
        }
      }
    },
    enableMessage() {
      this.active = !this.active;
      this.$socket.emit("changeMessageServer", this.active);
    },
    changeMessageRandomClient() {
      this.$socket.emit("messageRandom", this.messagesub);
    },
  },
};
</script>
