import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import User_Home from '../views/Users/Home.vue'
import PageNotFound from '../views/PageNotFound.vue'
import SubsYoutube from '../components/SubsYoutube.vue'
import MessageRandom from '../components/MessageRandom.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/subsYoutube',
    name: 'SubsYoutube',
    component: SubsYoutube
  },
  {
    path: '/messageRandom',
    name: 'MessageRandom',
    component: MessageRandom
  },
  {
    path: '/home',
    name: 'User_Home',
    component: User_Home
  },
  {
    path: '/servers',
    name: 'Servers',
    // route level code-splitting
    // this generates a separate chunk (Servers.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "Servers" */ '../views/Servers.vue')
  },
  { path: "*", component: PageNotFound }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
